import React, { useState } from "react";

const AuthSection = () => {
  const [URI, setURI] = useState();
  return (
    <div>
      <div>
        <input
          type="text"
          placeholder="URI"
          onChange={(e) => setURI(e.target.value)}
        />
        <button onClick={() => console.log(URI)}> set URI</button>
      </div>
      <div className="mt-50">
        <form action={`${URI}`} method="post">
          <label className="text-white" htmlFor="uname">
            <b>Username</b>
          </label>
          <input
            type="text"
            placeholder="Enter Username"
            name="uname"
            required
          />
          <label className="text-white" htmlFor="psw">
            <b>Password</b>
          </label>
          <input
            type="password"
            placeholder="Enter Password"
            name="psw"
            required
          />
          <input type="submit" value="Login" />
        </form>
      </div>
    </div>
  );
};

export default AuthSection;
